header{
    background-image: url(../img/ArcoJapones.jpg);
    background-position: center center;
    background-repeat: no-repeat;
    margin: 0% 0% 0% 0%;
    width: 100%;
    height: 41.5vh;
    display: inline-flex;
}

.contenedorHeader h1{
    position:static;
    text-shadow: 20px 30px grey;
    font-size: 10vw;
}
.contenedorHeader{
    width: 100%;
    height: 41vh;
}
.part1{
    float: left; 
    padding: 50px 0 0px 100px;
}
.part2{
    float: right;
    padding: 50px 100px 0px 0px;
}



@media screen and (max-width: 400px){

    .contenedorHeader h1{
       
        font-size: 14vw;
    }
    .contenedorHeader{
        width: 100%;   
    }
    .contenedorHeader h1{
        position:static;
        text-shadow: 10px 20px grey;
    }
    .part1{
        float: left;
        padding: 160px 0 0px 0px;
    }
    .part2{
        float: right;
        
        padding: 160px 0px 0px 0px;
    }


}

@media screen and (max-width: 900px) and (min-width: 401px){

    .contenedorHeader h1{
        font-size: 15vw;
    }
    .contenedorHeader{
        width: 100%;    
    }
    .part1{
        float: left;
        padding: 100px 0 100px 2vw;
    }
    .part2{
        float: right;
        padding: 100px 2vw 100px 0px;
    }
}



