footer{
    background-color: black;
    color: white;
    float: left;
    width: 100%;
    height: 40vh;
}

#textoFooter{
   
    display: inline-block;
    width: 50%;
    height: 0vh;
    
}

#textoFooter h3{
    font-size: 4vw;
    margin-top: 1%;
    text-align: center;   
    
}
#textoFooter p{
    font-size: 25px;
    text-align: center;
    margin: 2% 10% 0% 10%;
   
}
#redesSociales{
    
    float: right;
    list-style: none;
    width: 50%;
    height: 30vh;
    text-align: center;
    
    
}
#redesSociales h3{
    font-size: 4vw;
    margin-top: 1%;
    text-align: center;
}
.redes{

    font-size: 35px;
    margin-top: 5%;  
}
.redes li{
    margin-bottom: 2%;
}

#copyright{
    width: 50%;
    height: 0vh;
    margin: 2% 0% 0% 4%;
}
#copyright li{
    text-decoration:none;
    font-size: 20px;
    list-style: none;
    margin: 0% 0% 0% 2vw;
    float: left;
    
}
#copyright a{
    color: white;  
}

@media screen and (max-width: 500px){

    footer{
        background-color: black;
        color: white;
        float: left;
        width: 100%;
        height: 210vw;
        
    }

    #textoFooter{
        display: flex;
        width: 100%;
        height: 60vw; 
        order: 1;
        flex-direction: column;
        
    }
    #textoFooter h3{
        font-size: 12vw;
        margin-top: 1%;
        text-align: center;      
    }

    #textoFooter p{
        font-size: 4vw;
        text-align: center;
        margin: 10% 10% 0% 10%;
       
    }
    #redesSociales{
    
        order: 2;
        display: flex;
        flex-direction: column;
        list-style: none;
        margin-top: 10%;
        width: 100%;
        height: 68vw;
        text-align: center;

    }
    #redesSociales h3{
        font-size: 12vw;
        margin-top: 1%;
        text-align: center;
    }
    
    #redesSociales a{
        font-size: 12vw;
    }
    .redes{
        margin-top: 4%;
    
    }
    .redes li{
        margin: 0% 0% 0% 0%;
    }
    #copyright{
        width: 100%;
        
    }

    #copyright li{
        text-decoration: none;
        font-size: 5vw;
        list-style: none;
        margin: 10% 0% 0% 31%;
        
    }
}
@media screen and (max-width: 1100px) and (min-width: 501px){

    footer{
        background-color: black;
        color: white;
        float: left;
        width: 100%;
        height: 160vw;
    }
    #textoFooter{
        display: flex;
        width: 100%;
        height: 60vw; 
        order: 1;
        flex-direction: column;
        
    }
    #textoFooter h3{
        font-size: 10vw;
        margin-top: 5%;
        text-align: center;      
    }

    #textoFooter p{
        font-size: 4vw;
        text-align: center;
        margin: 2% 10% 0% 10%;
       
    }
    #redesSociales{
    
        order: 2;
        display: flex;
        flex-direction: column;
        list-style: none;
        width: 100%;
        height: 50vw;
        text-align: center;

    }
    #redesSociales a{

        font-size: 8vw;
    }
    #redesSociales h3{
    font-size: 10vw;
    margin-top: 1%;;
    text-align: center;
    }
    
    
    #copyright{
        width: 100%;
        height: 10vh;
    }

    #copyright li{
        font-size: 5vw;
        list-style: none;
        margin: 5% 0% 0% 31%;
    }
}

