.section1{
    
    background: linear-gradient(0deg,black,rgb(203, 203, 203),rgb(246, 246, 246));
    text-align: center;

    height: 55vh;
    float: left;
}
.section1 h1{
  font-size: 80px;
  margin: 2% 0% 0% 0%;
}
.section1 p{
    font-size: 22px;
    margin: 1% 20% 0% 20%;
}
.section2{
    background: linear-gradient(0deg,rgb(76, 76, 76),black,black,black,black);
    width: 100%;
    float: left;
    height: 90vh;
    color: white;
    z-index: 1;
}

#textoSection2{
    display: inline-block;
    width: 60%;
    height: 60vh;
    
}
#textoSection2 h1{

    font-size: 80px;
    padding: 5% 0% 1% 5%;
    text-align: left;

}
#textoSection2 p{

    font-size: 22px;
    text-align: left;
    padding: 2% 0% 0% 5%;
}
#imagenSection2 {

    width: 35%;
    height: 35%;
    float: right;

}
#imagenSection2 img{
    width: 50%;
    height: 70vh;
    margin-left: 30%;
    
}
#imagenAparicion {
    transition:  margin 1s, width 0s; 
}

#section3{
    width: 100%;
    height: 100vh;
    float: left;
    background: linear-gradient(0deg,black,rgb(75, 75, 75));
     
}
.ContenedorSeccion3{
    width: 100%;
    display: flex;
    position: relative;
}
.ContenedorTextoSeccion3{
   color: white;
   width: 53%;
   float: right;
   order: 2;
   margin: 2% 0% 0% 0%;
}
.ContenedorTextoSeccion3 h1{
    font-size: 80px;
    padding: 12% 0% 0% 5%;
}
.ContenedorTextoSeccion3 p{
    font-size: 22px;
    padding: 6% 5% 2% 5%;
}
.ContenedorImagenSeccion3{
    order: 1;
    position: relative;
    float: left;
    width: 40%;
    height: 95vh;
    margin: 0% 2% 0% 2%;
    
}

#imagenSeccion3Local{ 
    width: 70%;
    height: 80vh;
    margin: 4% 5% 0% 15%;
    border-radius: 2%;
    filter: brightness(100%);
    transition: margin 0.5s, height 0.5s, width 0.5s,;
}

#personajeAnime{

position: relative;
width: 0%;
height: 60vw;
margin: -80% 0% 0% 7%;
opacity: 0;
transition:  opacity  2s, width 0s, margin 2s;

}


@media screen and (max-width: 500px){

    .section1{
        height: 160vw;
    }
    .section1 h1{
        font-size: 15vw;
        margin: 5% 0% 0% 0%;
    }
    .section1 p{
          font-size: 4.5vw;
          margin: 5% 2% 0% 2%;
    }

    .section2{
        height: 320vw;
    }
    #textoSection2{
        float: left;
        width: 100%;
        height: 180vw;
        
    }
    #textoSection2 h1{
        font-size: 15vw;
        padding: 2% 0% 0% 0%;
        text-align: center;
    
    }
    #textoSection2 p{
    
        font-size: 4.5vw;
        text-align: center;
        padding: 5% 2% 0% 2%;
    }
    #imagenSection2{
        width: 100%; 
        float: left;    
    }
    #imagenSection2 img{
        width: 10%;
        height: 80vw;
        margin-left: 30%;
        margin-top: 50%;
    }

    #section3{
        height: 300vw; 
        display: flex;
        
    }
    .ContenedorSeccion3{
        width: 100%;
        display: flex;
        flex-direction: column;
    }
    .ContenedorTextoSeccion3{
       width: 100%;
       order: 1;
       margin: 0% 0% 0% 0%;
    }
    .ContenedorTextoSeccion3 h1{
        font-size: 15vw;
        text-align: center;
        padding: 0% 0% 0% 0%;
    }
    .ContenedorTextoSeccion3 p{
        font-size: 4.5vw;
        padding: 6% 5% 2% 5%;
        text-align: center;
    }
    .ContenedorImagenSeccion3{
        order: 2;
        width: 100%;
        height: 50vw;
        margin: 0% 0% 0% 0%;   
    }
    #imagenSeccion3Local{ 
        width: 70%;
        height: 120vw;
        margin: 8vw 0% 0% 15%;   
        float: left;
        z-index: 1;
        transition: margin 0.5s, height 0.5s, width 0.5s,;
        
    }
    #personajeAnime{
    position: relative;
    width: 0%;
    height: 30vh;
    margin: -110% 0% 0% 0%;
    opacity: 0;
    z-index: 6;
    transition:  opacity  1s, width 0s, margin 2s;
    }
    
}

@media screen and (max-width: 1100px) and (min-width: 501px){

    .section1{
        height: 80vw;
    }
    .section1 h1{
        font-size: 10vw;
        margin: 5% 0% 0% 0%;
    }
    .section1 p{
          font-size: 3vw;
          margin: 5% 2% 0% 2%;
    }

    .section2{
        
        width: 100%;
        height: 175vw;
        color: white;
        z-index: 1;
    }
    
    #textoSection2{
        float: left;
        width: 100%;
        height: 90vw;
        
    }
    #textoSection2 h1{
        font-size: 10vw;
        padding: 2% 0% 0% 0%;
        text-align: center;
    
    }
    #textoSection2 p{
    
        font-size: 3vw;
        text-align: center;
        padding: 5% 2% 0% 2%;
    }
    #imagenSection2 {

        width: 100%;
        float: left;
        
    }
    #imagenSection2 img{
        width: 10%;
        height: 70vw;
        margin-left: 30%;
        margin-top: 10%;
    }
    #section3{
        height: 175vw; 
        display: flex;    
    }
    .ContenedorSeccion3{
        width: 100%;
        display: flex;
        flex-direction: column;
    }
    .ContenedorTextoSeccion3{
       width: 100%;
       order: 1;
       margin: 0% 0% 0% 0%;
    }
    .ContenedorTextoSeccion3 h1{
        font-size: 10vw;
        text-align: center;
        padding: 0% 0% 0% 0%;
    }
    .ContenedorTextoSeccion3 p{
        font-size: 3vw;
        padding: 6% 5% 2% 5%;
        text-align: center;
    }
    .ContenedorImagenSeccion3{
        order: 2;
        width: 100%;
        height: 80vw;
        margin: 0% 0% 0% 0%;   
    }
    
    #imagenSeccion3Local{ 
        width: 60%;
        height: 80vw;
        margin: 5% 0% 0% 22%;   
        float: left;
        z-index: 1;
        transition: margin 0.5s, height 0.5s, width 0.5s,;   
    }
    
    #personajeAnime{
    position: relative;
    width: 0%;
    height: 30vh;
    margin: -110% 0% 0% 0%;
    opacity: 0;
    z-index: 6;
    transition:  opacity  1s, width 0s, margin 2s;
    }
    
}












