*{
 margin: 0;
 padding: 0;
 box-sizing: border-box;
}

#barra{
    width: 100%;
    height: 7vh;
    position: relative;
    margin-left: 0%;
    background-color: rgb(241, 241, 241);
    opacity: 0.8;   
}

#btnMostrarNav{
    display: none;   
}
#contenedorMenu{
    display: flex;
    list-style: none;  
}

#titleNav{
 display: none;
 margin: 0% 6% 0% 0%;
 font-size: 28px;
 height: 60px;
}
#btnOn{
    float: right;
    background-color: transparent;
    width: 4vh;
    border: none;
    display: none;
    margin-right: 2%;
    font-size: 40px;
    outline:none; 
}
#btnOn:hover{
    color:rgb(187, 187, 187);
    cursor: pointer;
}
#contenedorMenu a{
    display: block;
    font-size: 20px;
    color: white;  
    text-decoration: none;
}


#contenedorMenu div:hover{
    height: 7vh;
    background: rgba(0,0,0,0.3);
    
}

@media screen and (max-width: 930px){

    #btnMostrarNav{
        display: block;
        float: left;
        width: 40px;
        height: 40px;
        font-size: 40px;
        background-color: transparent;
        border: none;
        margin: 5px 0% 0% 2.5% ;     
        outline:none;
    }
    
    #contenedorMenu{
        position: absolute;
        top: 58px;
        display: block;
        background: rgb(124, 124, 124);
        left: -200%;
        
    }
    #contenedorMenu div{
        width: 100%;
        height: 7vh;
    }

    
    #titleNav{
        display: block;
        float: right;
        font-size: 30px;
        margin: 5px 5% 0% 0%;
        
    }
   
    #btnOn{
        display: block;
        margin: 0px 5% 0% 0%;
        float: right;
        position: relative;
        font-size: 50px;
    }
}
@media screen and (max-width: 1200px) and (min-width: 931px){


    #contenedorMenu li{
        height: 10px;
        padding: 18px 0% 10px 0%;
        
    }
    #contenedorMenu div{
        width: 15%;
        height: 6vh;
    }
    #contenedorMenu a{
        text-align: center;
        font-size: 25px;
        color: black;  
        text-decoration: none;
    }
    #contenedorMenu div{
        margin: 0% 0% 0% 50px;
    }
    #contenedorMenu{
        position: absolute;
        width: 70%;   
    }
   
    #titleNav{
        display: block;
        float: right;
        font-size: 30px;
        margin: 5px 5% 0% 0%;
        
    }
   
    #btnOn{
        display: block;
        margin: 0px 5% 0% 0%;
        float: right;
        position: relative;
        font-size: 50px;
    }
}

@media screen and (max-width: 3000px) and (min-width: 1201px){

    #contenedorMenu li{
        height: 10px;
        padding: 18px 0% 10px 0%;
        
    }
    #contenedorMenu div{
        width: 20%;
        height: 6vh;
    }
    #contenedorMenu a{
        text-align: center;
        font-size: 25px;
        color: black;  
        text-decoration: none;
    }
    
    #contenedorMenu{
        position: absolute;
        width: 70%;
        left: 0%; 
    }
   
    #titleNav{
        display: block;
        float: right;
        font-size: 30px;
        margin: 5px 5% 0% 0%;
        
    }
   
    #btnOn{
        display: block;
        margin: 0px 5% 0% 0%;
        float: right;
        position: relative;
        font-size: 50px;
    }
}